.logo-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #221f1f;
    gap: 1rem;

    padding: .5rem;
}

.logo-box .logo {
    width: 144px;
    height: 24px;
}

.logo-box .logo img {
    width: 100%;
    height: 100%;

    object-fit: cover;
}